.spinner-holder {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 999;
    background-color: #fff;
    height: 100vh;
    left: 0;
}

.spinner-holder .spinner {
    border-radius: 50%;
    width: 32px;
    height: 32px;
    font-size: 10px;
    position: relative;
    top: calc(50% - 16px);
    margin: 0 auto;
    text-indent: -9999em;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-left: 2px solid var(--first);
    transform: translateZ(0);
    animation: spinner 0.6s infinite linear;
}

